//Importing React since we are using React.
import React from "react";
// Importing UI components from material-ui-next
import Typography from 'material-ui/Typography';
import Input, { InputLabel, InputAdornment } from 'material-ui/Input';
import { MenuItem } from 'material-ui/Menu';
import { FormControl, FormHelperText } from 'material-ui/Form';
import Select from 'material-ui/Select';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';
import Card, { CardActions, CardContent } from 'material-ui/Card';
import { withStyles } from 'material-ui/styles';

const styles = {
    textField: {
        marginTop: 40
    },
    // Tell Material-UI what's the font-size on the html element is.
    typography: {
        htmlFontSize: 40
    },
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    formControl: {
        minWidth: 120,
    },
    button: {
        marginTop: 20,
        padding: 5
    },
};

class DoctorForm extends React.Component {
    state = {
        name: '',
        state: '',
        multiline: 'Controlled',
        doctor: '',
        clinic: '',
    };

    handleChange = name => event => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };

    render() {
        const { classes, theme } = this.props;

        return (
            <div>
                <Card>
                    <CardContent>
                        <Typography gutterBottom variant="headline" component="h2">
                            Add a doctor
                        </Typography>
                        <form noValidate className={classes.root} autoComplete="off">
                                <TextField
                                    id="first-name"
                                    label="First name"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    placeholder=""
                                    helperText=""
                                    fullWidth
                                    margin="normal"
                                    className={classes.textField}
                                />

                                <TextField
                                    id="last-name"
                                    label="Last name"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    placeholder=""
                                    helperText=""
                                    fullWidth
                                    margin="normal"
                                    className={classes.textField}
                                />
                                <FormControl className={classes.formControl, classes.textField} fullWidth> 
                                    <InputLabel htmlFor="select-clinic-dropdown">Select a clinic</InputLabel>
                                    <Select
                                        value={this.state.clinic}
                                        onChange={this.handleChange}
                                        inputProps={{
                                            clinic: '',
                                            id: 'select-clinic',
                                        }}
                                    >
                                        <MenuItem value=""></MenuItem>
                                        <MenuItem value={"A"}>Clinic A</MenuItem>
                                        <MenuItem value={"B"}>Clinic B</MenuItem>
                                        <MenuItem value={"C"}>Clinic C</MenuItem>
                                        <MenuItem value={"D"}>Clinic D</MenuItem>
                                        <MenuItem value={"Other"}>Other</MenuItem>
                                    </Select>
                                </FormControl>

                            <TextField
                                id="street-address"
                                label="Street address"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                className={classes.textField}
                                fullWidth
                            />

                            <TextField
                                id="doctor-city"
                                label="City"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                className={classes.textField}
                                fullWidth
                            />
                            <FormControl className={classes.formControl, classes.textField} fullWidth>
                                <InputLabel htmlFor="select-state-dropdown">State</InputLabel>
                                <Select
                                    value={this.state.state}
                                    onChange={this.handleChange}
                                    inputProps={{
                                        state: '',
                                        id: 'select-state',
                                    }}
                                >
                                    <MenuItem value=""></MenuItem>
                                    <MenuItem value={"AL"}>Alabama</MenuItem>
                                    <MenuItem value={"AK"}>Alaska</MenuItem>
                                    <MenuItem value={"AZ"}>Arizona</MenuItem>
                                    <MenuItem value={"CA"}>California</MenuItem>
                                    <MenuItem value={"CO"}>Colorado</MenuItem>
                                    <MenuItem value={"CT"}>Connecticut</MenuItem>
                                    <MenuItem value={"DE"}>Delaware</MenuItem>
                                    <MenuItem value={"FL"}>Florida</MenuItem>
                                    <MenuItem value={"GA"}>Georgia</MenuItem>
                                    <MenuItem value={"HI"}>Hawaii</MenuItem>
                                    <MenuItem value={"ID"}>Idaho</MenuItem>
                                    <MenuItem value={"IL"}>Illinois</MenuItem>
                                    <MenuItem value={"IN"}>Indiana</MenuItem>
                                    <MenuItem value={"IA"}>Iowa</MenuItem>
                                    <MenuItem value={"KS"}>Kansas</MenuItem>
                                    <MenuItem value={"KY"}>Kentucy</MenuItem>
                                    <MenuItem value={"LA"}>Louisiana</MenuItem>
                                    <MenuItem value={"ME"}>Maine</MenuItem>
                                    <MenuItem value={"MD"}>Maryland</MenuItem>
                                    <MenuItem value={"MA"}>Massachusetts</MenuItem>
                                    <MenuItem value={"MI"}>Michigan</MenuItem>
                                    <MenuItem value={"MN"}>Minnesota</MenuItem>
                                    <MenuItem value={"MS"}>Mississppi</MenuItem>
                                    <MenuItem value={"MO"}>Missouri</MenuItem>
                                    <MenuItem value={"MT"}>Montana</MenuItem>
                                    <MenuItem value={"NE"}>Nebraska</MenuItem>
                                    <MenuItem value={"NV"}>Nevada</MenuItem>
                                    <MenuItem value={"NH"}>New Hampshire</MenuItem>
                                    <MenuItem value={"NJ"}>New Jersey</MenuItem>
                                    <MenuItem value={"NM"}>New Mexico</MenuItem>
                                    <MenuItem value={"NY"}>New York</MenuItem>
                                    <MenuItem value={"NC"}>North Carolina</MenuItem>
                                    <MenuItem value={"ND"}>North Dakota</MenuItem>
                                    <MenuItem value={"OH"}>Ohio</MenuItem>
                                    <MenuItem value={"OK"}>Oklahoma</MenuItem>
                                    <MenuItem value={"OR"}>Oregon</MenuItem>
                                    <MenuItem value={"PA"}>Pennsylvania</MenuItem>
                                    <MenuItem value={"RI"}>Rhode Island</MenuItem>
                                    <MenuItem value={"SC"}>South Carolina</MenuItem>
                                    <MenuItem value={"SD"}>South Dakota</MenuItem>
                                    <MenuItem value={"TN"}>Tennessee</MenuItem>
                                    <MenuItem value={"TX"}>Texas</MenuItem>
                                    <MenuItem value={"UT"}>Utah</MenuItem>
                                    <MenuItem value={"VT"}>Vermont</MenuItem>
                                    <MenuItem value={"VA"}>Virginia</MenuItem>
                                    <MenuItem value={"WA"}>Washington</MenuItem>
                                    <MenuItem value={"WV"}>West Virginia</MenuItem>
                                    <MenuItem value={"WI"}>Wisconsin</MenuItem>
                                    <MenuItem value={"WY"}>Wyoming</MenuItem>
                                </Select>
                            </FormControl>

                            <TextField
                                id="doctor-zip-code"
                                label="Zip code"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                className={classes.textField}
                                fullWidth
                            />

                            <TextField
                                id="doctor-phone"
                                label="Phone number"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                className={classes.textField}
                                fullWidth
                            />

                        <Button size="large" className={classes.button} color="primary">
                            Add doctor
                        </Button>
                    </form>
                </CardContent>
            </Card>
        </div >
    );
    }
}

// TextFields.propTypes = {
//   classes: PropTypes.object.isRequired,
// };

export default withStyles(styles)(DoctorForm);


